.cards-box{
    width: 65%;
    display: inline-block;
}

.card {
    display: flex;
    align-items: center;
    padding-top: 20px;
}

.card img {
    width: 30%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
}

.card-content {
    flex: 1;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 15px;
    margin-right: 20px;
    margin-left: 20px;
}

.card-title {
    color: #666;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
}

.card-text {
    font-size: 1.4rem;
    color: #666;
    text-align: justify !important;
}
